import { createErrorFromUnknown } from '@grammarly/util-error'
import type { Logger } from '@grammarly/util-logging'

export function setupGlobalErrorHandling(logger: Logger): Disposable {
  using disposables = new DisposableStack()

  const handleErrorEvent = (event: ErrorEvent | PromiseRejectionEvent) => {
    const error =
      'error' in event ? createErrorFromUnknown(event.error) : createErrorFromUnknown(event.reason)
    const locInfo =
      'filename' in event ? ` at ${event.filename}:${event.lineno}:${event.colno}` : ''

    logger.error(`Uncaught error (${event.type})${locInfo}:`, error)
    logger.verbose('Details:', { event })
  }

  globalThis.addEventListener('unhandledrejection', handleErrorEvent)
  globalThis.addEventListener('error', handleErrorEvent)

  disposables.defer(() => {
    globalThis.removeEventListener('unhandledrejection', handleErrorEvent)
    globalThis.removeEventListener('error', handleErrorEvent)
  })

  return disposables.move()
}
