import '@grammarly/util-polyfills'

import { setEnv, type EnvConfig } from '@grammarly/util-env-config'
import { isDev } from '@grammarly/util-is-dev'
import { version } from '../package.json'
import { start } from './start'
import { startLegacy } from './startLegacy'

import './index.css'

const searchParams = new URL(window.location.href).searchParams

setEnv({
  name: 'inkwell',
  version,
  environment: isDev ? 'dev' : 'prod',
  platform: extractPlatform(searchParams.get('platform')),
  services: {
    environment: 'prod',
  },
})

const runLegacy = !searchParams.has('splitApp')

if (runLegacy) {
  void startLegacy()
} else {
  void start()
}

function extractPlatform(platform: string | null): EnvConfig['platform'] {
  const platformsRecord: Record<EnvConfig['platform'], null> = {
    dev: null,
    extension: null,
    windows: null,
  }
  return platform && platform in platformsRecord ? (platform as EnvConfig['platform']) : 'extension'
}
