import { UnreachableCaseError } from '@grammarly/util-error'
import { pipe } from '@grammarly/util-function'
import { Rx, type Subscribable } from '@grammarly/util-reactive'
import type { PointerEventDidEmitRequest } from '@grammarly/util-web-host-protocol'

/**
 * Catch minimal pointer events and synthesize events that we need.
 * see note at @see {@link PointerEventDidEmitRequest} for why we only send pointermove,
 * pointerdown, and pointerup.
 */
export function setupPointerForwarding(
  pointerEvents: Subscribable<PointerEventDidEmitRequest>,
): Disposable {
  let lastHoveredElement: Element | null = null

  // We're assuming that it's sufficient to send pointer events to only the topmost,
  // most deeply nested element at the pointer coordinates. The browser should bubble
  // it to any ancestors.
  // Elements that *won't* see the event are ones that are stacked behind the topmost
  // element with `pointer-events: auto` and not in the hierarchy above the target.
  // I believe that's what would happen for real events too, though.
  const elementFromPoint = (message: PointerEventDidEmitRequest) =>
    document.elementFromPoint(message.position.x, message.position.y)

  return pipe(
    Rx.fromSubscribable(pointerEvents),
    Rx.map(pointerEvent => ({
      pointerEvent,
      elementUnderPointer: elementFromPoint(pointerEvent),
    })),
    Rx.subscribe(({ pointerEvent, elementUnderPointer }) => {
      switch (pointerEvent.kind) {
        case 'pointermove': {
          if (elementUnderPointer !== lastHoveredElement) {
            if (lastHoveredElement) {
              lastHoveredElement.dispatchEvent(
                new PointerEvent('pointerleave', {
                  bubbles: true,
                  clientX: pointerEvent.position.x,
                  clientY: pointerEvent.position.y,
                }),
              )
              lastHoveredElement = null
            }
            if (elementUnderPointer) {
              lastHoveredElement = elementUnderPointer
              elementUnderPointer.dispatchEvent(
                new PointerEvent('pointerenter', {
                  bubbles: true,
                  clientX: pointerEvent.position.x,
                  clientY: pointerEvent.position.y,
                }),
              )
            }
          } else {
            elementUnderPointer?.dispatchEvent(
              new PointerEvent('pointermove', {
                bubbles: true,
                clientX: pointerEvent.position.x,
                clientY: pointerEvent.position.y,
              }),
            )
          }
          break
        }
        case 'pointerdown':
        case 'pointerup': {
          elementUnderPointer?.dispatchEvent(
            new PointerEvent(pointerEvent.kind, {
              bubbles: true,
              clientX: pointerEvent.position.x,
              clientY: pointerEvent.position.y,
            }),
          )
          break
        }
        default:
          throw new UnreachableCaseError(pointerEvent.kind)
      }
    }),
  )
}
