import { isDev } from '@grammarly/util-is-dev'
import { logger } from '@grammarly/util-logging'
import { AsyncResult, Result } from '@grammarly/util-result'
import {
  isSecureKeyMessage,
  isSecureOriginMessage,
  type TransferPorts,
} from '@grammarly/util-web-host-protocol'
import { getExtensionSecurityKey } from './extensionAPI'

export async function setupMessagingPorts(): AsyncResult<TransferPorts> {
  const messagePortReceived = Promise.withResolvers<Result<TransferPorts>>()

  const onWindowPostMessage = async (event: MessageEvent): Promise<void> => {
    if (!event.isTrusted || !event.source || event.source !== window.parent) {
      logger.error('Non trusted event')
      return
    }

    if (isSecureKeyMessage(event)) {
      const key = await getExtensionSecurityKey()
      if (!key) {
        logger.error('Cannot obtain a security key')
      } else if (event.data.secureKey === key) {
        logger.verbose('Inkwell Iframe, Port received')
        messagePortReceived.resolve(
          Result.success({
            webHostPort: event.data.webHostPort,
            connectorPort: event.data.connectorPort,
          }),
        )
        window.removeEventListener('message', onWindowPostMessage)
      } else {
        logger.error(`Security key doesn't match`)
      }
    } else if (
      isDev &&
      event.origin.startsWith('http://localhost:') &&
      isSecureOriginMessage(event)
    ) {
      messagePortReceived.resolve(
        Result.success({
          webHostPort: event.data.webHostPort,
          connectorPort: event.data.connectorPort,
        }),
      )
      window.removeEventListener('message', onWindowPostMessage)
    } else {
      logger.warn('Cannot process incoming post message, it seems to have wrong data')
    }
  }
  window.addEventListener('message', onWindowPostMessage)
  return await messagePortReceived.promise
}
