import { launchLegacyDecorationsApp } from '@grammarly/app-web-client'
import type { ConnectorID } from '@grammarly/feature-connector'
import { pipe } from '@grammarly/util-function'
import { ScreenRect } from '@grammarly/util-geometry'
import { Rx } from '@grammarly/util-reactive'
import { isDev } from '@grammarly/util-is-dev'
import { ConsoleLogWriter, FeLogWriter, logger, MemoryLogWriter } from '@grammarly/util-logging'
import { AsyncResult, Result } from '@grammarly/util-result'
import { DefaultRemoteFunctionManager, PortMessageTransport } from '@grammarly/util-rpc'
import {
  type HostToWindow,
  type PointerEventDidEmitRequest,
  type WindowToHost,
} from '@grammarly/util-web-host-protocol'
import { version } from '../package.json'
import { setupGlobalErrorHandling } from './errorHandling'
import { setupMessagingPorts } from './setupMessagingPort'
import { setupPointerForwarding } from './setupPointerForwarding'

/**
 * Entry point of the application.
 */
export async function startLegacy() {
  await using disposables = new AsyncDisposableStack()
  disposables.use(setupGlobalErrorHandling(logger))

  let removeConsoleLog: Disposable | null = null
  logger.addWriter(
    new FeLogWriter({
      application: 'Inkwell',
      environment: isDev ? 'development' : 'production',
      version,
      client: 'extension',
      sessionId: crypto.randomUUID(),
      logsPerMinute: 50,
      logsPerSecond: 20,
    }),
  )
  if (isDev) {
    removeConsoleLog = logger.addWriter(new ConsoleLogWriter())

    // Use MemoryLogWriter to store logs in memory and copy them to the clipboard.
    const writer = new MemoryLogWriter()
    logger.addWriter(writer)
    Reflect.set(window, 'copyLogsToClipboard', () => writer.copyToClipboard())
    Reflect.set(window, 'clearLogs', () => writer.clear())
    console.log(
      'Log messages are stored in memory. Use copyLogsToClipboard() to copy them to the clipboard.',
    )

    // Make the iframe window background visible in dev to simplify debugging.
    document.body.setAttribute('style', 'background-color: rgba(50, 200, 50, 0.1);')
  }

  const portResult = await setupMessagingPorts()
  if (!portResult.ok) {
    logger.error('Failed to setup messaging port', portResult.error)
    return disposables.move()
  }

  const { webHostPort, connectorPort } = portResult.value

  const pointerEvents = new Rx.Subject<PointerEventDidEmitRequest>()
  let windowRect: ScreenRect = ScreenRect.create(document.body.getBoundingClientRect())

  const windowRPC = disposables.use(
    new DefaultRemoteFunctionManager<HostToWindow, WindowToHost>(
      new PortMessageTransport({
        port: webHostPort,
        getMetadata: () => ({}),
        name: 'app-target-web-WindowToHost',
      }),
      {
        handler: {
          windowRectDidUpdate: ({ screenRect }) => {
            windowRect = screenRect
            return AsyncResult.success()
          },
          pointerEventDidEmit: request => {
            pointerEvents.next(request)
            return AsyncResult.success()
          },
        },
      },
    ),
  )

  const windowToHostClient = windowRPC.createClient<WindowToHost>()

  // We are not interested in response
  void windowToHostClient.execute('connected', undefined)

  const app = await launchLegacyDecorationsApp({
    appWindow: {
      root: document.getElementById('app') as HTMLElement,
      setScreenRect: screenRect => windowToHostClient.execute('updateWindowRect', { screenRect }),
      get screenRect() {
        return windowRect
      },
    },
    connectorTransport: new PortMessageTransport({
      port: connectorPort,
      getMetadata: () => ({ connectorId: 'parent-connector' as ConnectorID }),
      name: 'app-target-web-connector',
    }),
    connectorConfiguration: {
      getNonce() {
        return Promise.resolve('no-nonce-needed')
      },
      connectorTimeoutMs: 5000,
    },
  })

  pipe(
    app,
    Result.match({
      success: app => {
        disposables.use(app)
        disposables.use(setupPointerForwarding(Rx.toSubscribable(pointerEvents)))
        disposables.use(
          app.onDebug.subscribe(({ enabled }) => {
            if (enabled) {
              removeConsoleLog ??= logger.addWriter(new ConsoleLogWriter())
            } else if (!isDev) {
              removeConsoleLog?.[Symbol.dispose]()
              removeConsoleLog = null
            }
          }),
        )
      },
      failure: err => logger.error('Failed to start app:', err),
    }),
  )

  return disposables.move()
}
