import { SECURE_KEY_REQUEST_MESSAGE } from '@grammarly/util-web-host-protocol'
import { logger } from '@grammarly/util-logging'

/**
 * Retrieves the security key for the extension communication by asking background page.
 *
 * @returns {Promise<string | null>} A promise that resolves to the security key as a string.
 * If the API is not available, the promise resolves to null.
 */
export async function getExtensionSecurityKey(): Promise<string | null> {
  const api =
    typeof chrome !== 'undefined' ? chrome : (Reflect.get(globalThis, 'browser') as typeof chrome)

  if (typeof api !== 'undefined' && api.runtime && api.runtime.sendMessage) {
    return await api.runtime.sendMessage<string, string>(SECURE_KEY_REQUEST_MESSAGE)
  } else {
    logger.error('Chrome runtime is not available')
    return null
  }
}
